<template>
	<div class="mine">
		<div class="user_header">
			<h2 class="wid1200">用户管理系统</h2>
		</div>
		<div class="user_main wid1200">
			<div class="user_nav">
				<ul>
					<li>会员查询</li>
					<li>新增会员</li>
					<li>会员列表</li>
					
				</ul>
			</div>
		</div>
		
	</div>
</template>

<script>
export default {
	name: 'user',
	data () {
		return {
			msg: 'Welcome to BeiJing',
			name: 'xiaoming'
		}
	},
	mounted(){
		this.$http.get('http://localhost/bbmall/api/controller/getCustomer.php?shopno=1').then(function (res){
		console.log(res);
		})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
	font-weight: normal;
}

ul {
	list-style-type: none;
	padding: 0;
}

li {
	display: inline-block;
	margin: 0 10px;
}

a {
	color: #42b983;
}
.wid1200 {
	width : 1200px;
	margin: 0 auto;
}
.user_header{
	height: 65px;
	background: #148cca;
	font-size: 24px;
	line-height: 65px;
	color: #ffffff;
}
.user_main{
	position:relative;
}
.user_nav{
	position:absolute;
	top:0;
	left:0;
	width: 135px;
    font-size: 20px;
    border-right:1px solid #e6e6e6;
}
.user_nav li{
	height:54px;
	line-height:54px;
	color:#333333;
}
</style>


